.Tag {
    height: 50%;
    width: 100px;
    border-radius: 5px;
    background-color: #2f3640;
    color: #dcdde1;
    margin: 1%;
    font-size: 70%;
    
}

.Tag:hover {
    background-color: #dcdde1;
    color: #2f3640;
}
.AppScreen {
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}

.Card {
    box-shadow: 0 4px 4px -4px #00adb5;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    width: 30%;
    margin-bottom: 2%;
    position: relative;
}

.Card span {
    visibility: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    color: #f5f6fa;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    line-height: 100%;
    
}

.Card:hover {
    box-shadow: 0 4px 4px 0 #00adb5;  
}

.Card:hover .AppScreen {
    -webkit-filter: none;
            filter: none;
    -webkit-filter: blur(1px);
            filter: blur(1px);
    opacity: 0.4;
}

.Card:hover span {
    visibility: visible;
    -webkit-transition: all 0.2s linear;
            transition: all 0.2s linear;
}

.Tags {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: space-between;
    color: #dcdde1;
}

@media screen and (max-width: 600px) {
    .Card {
        width: 100%;
        margin-bottom: 5%; 
    }
}

.Container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

@media screen and (max-width: 600px) {
    .Container {
        flex-direction: column;
        justify-content: space-between;
    }
}

.topnav {
    overflow: hidden;
    background: #414141;
    position: fixed;
    width: 100%;
    height: 10%;
  }

  .button {
    border: none;
    color: #fff7f7;
    padding: 10px;
    font-size: x-large;
    border-radius: 5px;
    position: relative;
    box-sizing: border-box;
    text-decoration: none;
  }
  
  .button:hover {
    width: 100%;
    background: #fff7f7;
    color: #252525;
  }
  
  .topnav .icon {
    display: none;
  }
  
  @media screen and (max-width: 600px) {
    .topnav a:not(:first-child) {display: none;}
    .topnav a.icon {
      /*float: right;
      display: block;*/
      display: none;
    }
    .button {
      font-size: large;
    }
  }
  
  @media screen and (max-width: 600px) {
    .topnav {
      padding: 0%;
    }

    .topnav.responsive {position: relative;}
    .topnav.responsive .icon {
      /*position: absolute;
      right: 0;
      top: 0;*/
      display: none;
    }
    .topnav.responsive a {
      float: none;
      display: inline;
      text-align: left;
      padding: 5px;
    }
  }
